.screen-order-complete{
    width: 100%;
    height: 100%;
    background-color: var(--color-bg);
    z-index: 1;
}

.screen-order-complete img{
    width: 20%;
    margin-block-start: var(--margin-lg);
    margin-block-end: var(--margin-md);
    filter: invert(22%) sepia(26%) saturate(2128%) hue-rotate(132deg) brightness(85%) contrast(102%);
    animation: pulse 1.5s infinite ease-in-out;

}

.screen-order-complete h1{
    margin-block-end: var(--margin-sm);
}

.screen-order-complete h4{
    margin-block-start: var(--margin-md);
    margin-block-end: var(--margin-sm);

}

@keyframes pulse {
    0% {
      transform: scale(1);
    }
    80% {
      transform: scale(1.05);
    }
    100% {
      transform: scale(1);
    }
  }