@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fraunces:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Notable&family=Vina+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Courier+Prime:ital,wght@0,400;0,700;1,400;1,700&display=swap");

* {
  box-sizing: border-box;

  margin: 0;
  padding: 0;
}

p {
  font-size: 14px;
}

h1 {
  font-size: 22px;
  font-weight: bold;
}

h2 {
  font-size: 18px;
  font-weight: bold;
}

h3 {
  font-size: 16px;
  font-weight: bold;
}

h4 {
  font-size: 14px;
  font-weight: bold;
}

button {
  font-family: "Courier Prime", monospace;
  margin-top:4px ;
  width: 100%;
  background-color: var(--color-bg);
  color: var(--color-dark);
  font-size: 14px;
  border: 1px solid black;
}

body {
  font-family: "Courier Prime", monospace;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(67, 102, 121);
  color: var(--color-dark);
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url("https://www.transparenttextures.com/patterns/otis-redding.png");
  opacity: 0.5;
  pointer-events: none;
  z-index: 10000;
}

:root {
  --color-prim: rgb(0, 69, 61);
  --color-sec: rgb(176, 65, 62);
  --color-bg: #f6eee2;
  --color-dark: rgb(11, 18, 21);
  --color-grey: rgb(137, 137, 137);
  --border-radius: 52px;
  --border-radius-sm: 18px;
  --margin-lg: 32px;
  --margin-md: 16px;
  --margin-sm: 8px;
  --icon-width: 20px;
}

.App {
  text-align: center;
}

.App-cont {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 98vh;
}

.mobile-cont {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  background-color: var(--color-bg);
  border: 2px solid black;
  border-radius: var(--border-radius);
  height: 90vh;
  max-height: 650px;
  aspect-ratio: 9/18;
  cursor: pointer;
  overflow: hidden;
}

.scroll-area{
  padding-inline: 10px;
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  overflow-x: hidden;
}


.scroll-area::-webkit-scrollbar {
  display: none;
}

.page-cont {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  overflow: hidden;
}

.dynamic-island {
  position: absolute;
  top: var(--margin-md);
  background-color: black;
  width: 35%;
  height: 30px;
  border-radius: var(--border-radius);
  z-index: 1000;
}

.s-point {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: calc(var(--margin-md) * 3);
}

.s-point img {
  width: var(--icon-width);
}

.menu {
  font-family: "Notable", sans-serif;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: calc(var(--margin-md) * 2);
  font-size: 100px;
  color: var(--color-dark);
}

@keyframes fadeIn-box {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-box {
  animation: fadeIn-box 0.8s ease-in-out;
}

.fade-in-1 {
  animation: fadeIn-box 1.1s ease-in-out;
}

.fade-in-2 {
  animation: fadeIn-box 2.0s ease-in-out;
}

@media (max-width: 450px) {

  body {
    overflow: auto;
  }

  .App-cont {
    height: 100vh;
  }

  .mobile-cont {
    border: none;
    border-radius: 0px;
    height: 100vh;
    max-height: 50000px;
    aspect-ratio: 1;
    overflow: auto;
  }

  .dynamic-island {
    display: none;
  }

  .menu {
    margin-top: 0px;
  }
}
