.screen-scan {
  margin-top: var(--margin-lg);
}

.screen-scan h4 {
  font-size: 14px;
  font-weight: 100;
}

.screen-scan p {
  font-size: 12px;
  font-weight: 100;
}

.qr-wrapper {
  position: relative;
  width: 80%;
  margin: var(--margin-lg) auto;
}

.qr-wrapper img {
  display: block;
  width: 100%;
  margin-block: calc(var(--margin-lg) * 2);
  padding: var(--margin-sm);
  background-color: white;
  border-radius: var(--border-radius-sm);
  border: 1px solid rgb(227, 227, 226);
  animation: fadeIn 4s ease-in forwards;
}

.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  background: rgba(255, 63, 63, 0.487);
  animation: scanMove 6s linear infinite;
  z-index: 1;
  border-radius: var(--border-radius-sm);
}

.redir-msg{
    color: rgb(137, 137, 137);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes scanMove {
  0% {
    top: 0%;
  }
  50% {
    top: 100%;
  }
  100% {
    top: 0%;
  }
}
