.scr-pmnt {
  position: relative;
  height: 85vh;
  width: 100%;
  overflow-y: hidden;
}

.payment {
  font-family: "Notable", sans-serif;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: calc(var(--margin-md) * 2);
  font-size: 55px;
  color: var(--color-dark);
}

.radio-btns {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
}

.method {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: var(--margin-sm);
}

.method img {
  width: 46px;
  background-color: white;
  border-radius: 8px;
}

.info {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: var(--margin-sm);
}

.back-btn {
  border: none;
  background-color: transparent;
  padding-block: var(--margin-md);
}

.pay-div {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.pay-island {
  display: block;
  align-items: center;
  justify-content: start;
  position: absolute;
  bottom: calc(var(--margin-lg) * 2);
  background-color: var(--color-prim);
  width: 90%;
  height: 30px;
  color: var(--color-bg);
  padding: 8px;
}

input[type="radio"] {
  appearance: none;
  -webkit-appearance: none;
  border: 1px solid rgba(132, 111, 47, 0.488);
  border-radius: 50%;
  width: 14px;
  height: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

input[type="radio"]:checked {
  background-color: var(--color-dark);
  border-color: var(--color-dark);
}

.spinner {
  width: 24px;
  height: 24px;
  border: 6px solid var(--color-dark);
  border-top: 6px solid transparent;
  border-radius: 64px;
  animation: spin 1s linear infinite;
  margin: auto;
  margin-top: calc(var(--margin-lg) * 8);
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 450px) {
  .scr-pmnt {
    position: relative;
    height: 85vh;
    width: 100%;
    overflow: hidden;
    overflow-y: hidden;
  }

  .pay-island {
    bottom: calc(var(--margin-lg) * 2);
    height: 36px;
  }

  .pay-island {
    bottom: calc(var(--margin-lg) * 3);
  }

  .back-btn {
    position: relative;
    bottom: 120px;
  }
}
