.category-title {
  width: 100%;
  text-align: start;
}

.category-title h1 {
  font-size: 14px;
  font-weight: 100;
  text-decoration: underline;
}

.sp-div {
  width: 100%;
  height: 64px;
}

.products-list {
  width: 100%;
}

.product-line {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-top: var(--margin-md);
}

.area {
  width: 100%;
  height: 20px;
}

.product-cont {
  text-align: start;
  width: 100%;
  margin-top: var(--margin-lg);
}

.product-title {
  display: flex;
  align-items: center;
  width: 100%;
}

.product-title p {
  text-align: start;
  font-size: 16px;
}

.product-descr p {
  font-size: 12px;
}

.order-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.order-island {
  display: block;
  align-items: center;
  justify-content: start;
  position: absolute;
  bottom: var(--margin-lg);
  background-color: var(--color-sec);
  width: 85%;
  height: 30px;
  color: var(--color-bg);
  padding: 8px;
}

.gen-btn {
  border: none;
}

.order-island-txt {
  text-align: start;
  white-space: nowrap;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.total-order-div {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.total-order {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
  position: absolute;
  bottom: var(--margin-lg);
  background-color: var(--color-sec);
  width: 85%;
  height: 200px;
  color: var(--color-bg);
  padding: 8px;
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  overflow-x: hidden;
}

.scroll-area::-webkit-scrollbar {
    display: none;
  }

.total-order-header {
  width: 100%;
  display: flex;
  align-items: end;
  justify-content: space-between;
}

.total-order-header button {
  background-color: var(--color-sec);
  color: white;
  width: 10%;
  margin-top: 0px;
  border: none;
}

.total-order-list {
  width: 100%;
}

.order-product-line {
  margin-top: var(--margin-sm);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: start;
}

.order-product-line button {
  width: 20%;
  text-align: center;
  margin-top: 0px;
  background-color: var(--color-sec);
  border: none;
  color: var(--color-bg);
}

.pay-btn {
  background-color: var(--color-bg);
  border: none;
  height: 32px;
  margin-top: var(--margin-md);
}

.popup-payment{
  position: relative;
  width: 100%;
  display: flex;
  align-items: start;
  justify-content: start;
}

.pay-method {
  position: absolute;
  width: 100%;
  border:1px solid var(--color-dark);
  background-color: var(--color-bg);
  text-align: center;
  z-index: 1000;
}

.pay-methods-div {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pay-methods {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
  position: absolute;
  top: 0px;
  background-color: var(--color-bg);
  width: 100%;
  height: 100%;
  color: var(--color-dark);
  padding: 8px;
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  overflow-x: hidden;
  z-index: 100;
}

.back-btn{
  width: 100%;
}

@media (max-width: 450px) {
  .order-island {
    bottom: calc(var(--margin-lg) * 3.2);
    height: 36px;
  }

  .total-order {
    bottom: calc(var(--margin-lg) * 3.2);
    width: 85%;
    height: 200px;
    color: var(--color-bg);
    padding: 8px;
    overflow-y: auto;
  }
}
